@media  screen and (max-width:700px) {
   .main{
    grid-template-columns: 1fr;

   }
   .city{
    z-index: 10;
    position: fixed;
    top: 1em;
    left: 0.4em;
    height: 4em;
    width: 20em;
   }
   .main{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
   }
   #box{
    width: 40em;
   }
   .nav{
    justify-content: flex-end;
    align-items: center;
    border-radius: 0%;
    z-index: 5;

   }
   .nav div h1{
    display: none;

   }
   input[type="text"]{
    width: 10em;
   }
   .nav div{
    margin-right: 2em;
   }
}

@media  screen and (max-width:1080px) {
    body{
        color: greenyellow;
    }

}