@import url('https://fonts.googleapis.com/css2?family=Bodoni+Moda:opsz@6..96&family=Lobster&family=Nunito:wght@300&family=Roboto:wght@300&display=swap');

*{
    margin: 0%;
    padding: 0%;
    box-sizing: border-box;
}
body{
    background-color: red;
}
html{
    font-size: 62.5%;
}

.nav{
    background-color: red;
    width: 100vw;
    height: 6em;
    display: flex;
    justify-content: flex-end;
    border-radius: 0px 0px 50px 50px;
    align-items:center;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
    position: fixed;
    top: 0;
    left: 0;
}

.nav div{
    display: flex;
    margin-right: 4em;
   
}
input[type="text"]{
    width: 20em;
    height: 2.5em;
    border-radius: 8px 0px 0px 8px;
    border: none;
}
input[type="text"]:focus{
    outline: none;
    border: 1px solid rgb(178, 236, 18);

}
.nav div button{
    width: 4em;
    height: 2.5em;
    border-radius: 0px 8px 8px 0px;
    border: none;
    cursor: pointer;
    font-family: 'Lobster', cursive;
    transition: 1s;
}
.nav div button:hover{
    background-color: red;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
    border: 1px solid gray;

}
.nav div h1{
    color: white;
    font-family: 'Lobster', cursive;
    margin-right: 9em;
    font-size: 30px;
    animation: linear 2s linear infinite alternate;
    &:hover{
        animation-play-state: paused;
    }
}
@keyframes linear{
    to{
        transform: translateX(-10px);
    }
}
.main{
    margin-top: 7em;
    width: 100vw;
    display: grid;
    row-gap: 1em;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
}
#box{
    background-color: red($color: #000000);
    height: 32.5em;
    width: 30em;
    border-radius: 50px;
    margin: .2em;  
    display: flex;
    align-items: center; 
    flex-direction: column;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px; 
    &:hover{
        animation-play-state: paused;
    }
}
.box1{
    animation: up 1.2s linear infinite alternate;

}
.box2{
    animation: down 1.2s linear infinite alternate;

}.box3{
    animation: up 1.2s linear infinite alternate;

}.box4{
    animation: down 1.2s linear infinite alternate;

}.box5{
    animation: up 1.2s linear infinite alternate;

}.box6{
    animation: down 1.2s linear infinite alternate;

}
.box7{
    animation: up 1.2s linear infinite alternate;

}.box8{
    animation: down 1.2s linear infinite alternate;

}.box9{
    animation: up 1.2s linear infinite alternate;

}.box10{
    animation: down 1.2s linear infinite alternate;

}
@keyframes up{
    to{
        transform: translateY(-5px);
    }
}
@keyframes down{
    to{
        transform: translateY(-10px);
    }
}
.heading > h1{
    margin-top: .6em;
    color: white;
    font-size: 40px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px; 
    padding: .2em;
    border-radius: 50px;
    font-family: 'Lobster', cursive;
}
.info{
    margin-top: 1em;
    color: white;
    font-family: 'Lobster', cursive;
    font-size: 50px;

}
span{
    opacity: .7;
}
.city{
    display: flex;
    justify-content: center;
    position: absolute;
    color: white;
    top: .5em;
    left: 8em;
    height: 5em;
    width: 28em;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px; 
    border-radius: 50px;
    >h1{
        color: white;
    font-family: 'Lobster', cursive;
    font-size: 30px;
    }
}