@import url("https://fonts.googleapis.com/css2?family=Bodoni+Moda:opsz@6..96&family=Lobster&family=Nunito:wght@300&family=Roboto:wght@300&display=swap");
* {
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
}

body {
  background-color: red;
}

html {
  font-size: 62.5%;
}

.nav {
  background-color: red;
  width: 100vw;
  height: 6em;
  display: flex;
  justify-content: flex-end;
  border-radius: 0px 0px 50px 50px;
  align-items: center;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
  position: fixed;
  top: 0;
  left: 0;
}

.nav div {
  display: flex;
  margin-right: 4em;
}

input[type=text] {
  width: 20em;
  height: 2.5em;
  border-radius: 8px 0px 0px 8px;
  border: none;
}

input[type=text]:focus {
  outline: none;
  border: 1px solid rgb(178, 236, 18);
}

.nav div button {
  width: 4em;
  height: 2.5em;
  border-radius: 0px 8px 8px 0px;
  border: none;
  cursor: pointer;
  font-family: "Lobster", cursive;
  transition: 1s;
}

.nav div button:hover {
  background-color: red;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
  border: 1px solid gray;
}

.nav div h1 {
  color: white;
  font-family: "Lobster", cursive;
  margin-right: 9em;
  font-size: 30px;
  -webkit-animation: linear 2s linear infinite alternate;
          animation: linear 2s linear infinite alternate;
}
.nav div h1:hover {
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}

@-webkit-keyframes linear {
  to {
    transform: translateX(-10px);
  }
}

@keyframes linear {
  to {
    transform: translateX(-10px);
  }
}
.main {
  margin-top: 7em;
  width: 100vw;
  display: grid;
  row-gap: 1em;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
}

